<template>
  <container :title="title">
    <list :show-del="true">
      <thead slot="thead">
      <tr>
        <th width="35"><checkbox v-model="checked" :checked="tgCheck"/></th>
        <th>{{lang.id}}</th>
        <th v-if="!guestbook">{{lang.arc_title}}</th>
        <th>{{lang.username}}</th>
        <th>{{lang.reply_target}}</th>
        <th>{{lang.email}}</th>
        <th v-if="!guestbook">{{lang.create_time}}</th>
        <th v-if="guestbook">{{lang.message_time}}</th>
        <th>{{lang.status}}</th>
        <th width="125">{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
      <tr v-for="vol in list">
        <td><checkbox  v-model="checkbox" :checked="checked" :value="vol.id"/></td>
        <td>{{vol.id}}</td>
        <td v-if="!guestbook">{{vol.title}}</td>
        <td>{{vol.username}}</td>
        <td>{{vol.reply_target}}</td>
        <td>{{vol.email}}</td>
        <td>{{vol.create_time}}</td>
        <td>
          <el-switch
            v-model="vol.status"
            @change="changeState(vol)"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </td>
        <v-td type="opt" edit-route="comment_edit" :guestbook="guestbook" :value="vol.id" />
      </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd, Checkbox} from "@/components"

  export default {
    components: {Checkbox, VTd,Container, List},
    data(){
      return{
        // title:this.$t('comment.index'),
        lang:this.$t('comment'),
        guestbook: false,
        checked:false,
        tgCheck:false,
      }
    },
    watch:{
      checkbox(newValue){
        const length = newValue.length
        if (length === this.list.length){
          this.tgCheck = true
        }
        if (length === 0) {
          this.tgCheck = false
        }
      },
      $route:'fetchList'
    },
    computed:{

      title(){
        return this.guestbook? this.$t('comment.guestbookIndex'):this.$t('comment.index')
      },
      list(){
        return this.$store.state.table.list
      },
      checkbox:{
        get(){
          return this.$store.state.table.checkbox
        },
        set(value){
          this.$store.commit('table/SET_CHECKBOX',value)
        }
      },
    },
    methods:{
      changeState(comment){
        const status = comment.status?1:-1;
        const data = {
          id: comment.id,
          status
        }
        this.$store.dispatch('comment/refState', data)
      },
      fetchList(){
        this.guestbook = this.$route.meta.guestbook
        let url = ''
        if (this.guestbook){
          url = '/api/comment/guestBook/index'
        } else{
          url = '/api/comment/archive/index'
        }
        this.$store.commit('table/SET_URL', url)
        this.$store.dispatch('table/fetchList')
      }
    },
    mounted(){
     this.fetchList()
    }
  }
</script>



